import React, { useState } from 'react';
import colorTokens from './merged-colors.json';

const tokens = colorTokens.color;

const DesignTokensDisplay = () => {

  const [searchQuery, setSearchQuery] = useState('');
  const [filteredTokens, setFilteredTokens] = useState(colorTokens.color);

  const handleSearch = (e) => {
      const { value } = e.target;
      setSearchQuery(value);

      // Filter tokens based on the search query
      const filtered = Object.keys(colorTokens.color).reduce((acc, tokenName) => {
        if (tokenName.toLowerCase().includes(value.toLowerCase())) {
          acc[tokenName] = colorTokens.color[tokenName];
        }
        return acc;
      }, {});

      setFilteredTokens(filtered);
  };

  const copyToClipboard = (value) => {
    // Create a temporary textarea element to copy the value to the clipboard
    const tempTextArea = document.createElement('textarea');
    tempTextArea.value = value;

    // Append the textarea to the DOM
    document.body.appendChild(tempTextArea);

    // Select the textarea and copy the value to the clipboard
    tempTextArea.select();
    document.execCommand('copy');

    // Remove the textarea from the DOM
    document.body.removeChild(tempTextArea);

    // Optionally, provide visual feedback to the user
    alert(`Copied: ${value}`);
  };


  return (
    <div>
      <h2>Color Tokens</h2>
      <input
        type="text"
        placeholder="Search tokens..."
        value={searchQuery}
        onChange={handleSearch}
        style={{ marginBottom: '10px' }}
      />
      <table style={{ width: '100%', borderCollapse: 'collapse', marginTop: '10px' }}>
        <thead>
          <tr>
            <th>Token Name</th>
            <th>Light Value</th>
            <th>Dark Value</th>
          </tr>
        </thead>
        <tbody>
          {Object.entries(filteredTokens).map(([tokenName, tokenData]) => (
            <tr key={tokenName}>
              <td style={{ border: '1px solid #ddd', padding: '8px' }}>{tokenName}</td>
              <td style={{ border: '1px solid #ddd', padding: '8px' }}><div className="textColor light" onClick={() => copyToClipboard(tokenData.value)}>{tokenData.value}</div> <div className="preview light" style={{ backgroundColor: tokenData.value }}></div></td>
              <td style={{ border: '1px solid #ddd', padding: '8px' }}><div className="textColor dark" onClick={() => copyToClipboard(tokenData.darkValue)}> {tokenData.darkValue}</div> <div className="preview dark" style={{ backgroundColor: tokenData.darkValue }}></div></td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};


export default DesignTokensDisplay;